.header {
  position: absolute;
  top: 0;
  z-index: 5;

  width: 100%;
  margin-top: 2.6rem;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {}

.header__phone-wrapper {
  text-align: right;
}

.header__phone {
  display: inline-block;

  font-size: 2.2rem;
  font-weight: @heavy;
}

.header__phone-text {
  font-size: 1.7rem;
}

.header__menu-toggle,
.header__link-call-back {
  display: none;
}

